	// 			padding: 0 15px
	.search__blue
		+r(1355)
			float: left
			margin-left: 30px
			width: 100%
			margin-top: 10px
		+r(700)
			width: 100%
			margin: 10px 0 0
	.title__main
		+r(1355)
			width: 95%
		+r(700)
			width: 100%
			margin: 0 0 15px



.search__blue_wrap
	position: relative
	width: 240px
	+clr
	+r(800)
		width: 190px
	&:hover
		.search__field
			width: 240px
			+placeholder
				color: #fff
			+r(800)
				width: 190px
		label
			opacity: 0
			+tr(.3s)
.search__blue
	position: relative
	float: right
	height: 65px
	label
		position: absolute
		left: 10px
		display: inline-block
		font-size: 18px
		color: $black
		line-height: 65px
		margin-right: 30px
		vertical-align: top
		+os
		z-index: 1
		+r(900)
			margin-right: 15px
	.search__field
		position: relative
		float: right
		padding: 0 15px
		vertical-align: top
		margin-top: 18px
		width: 50px
		height: 34px
		background-color: $blue
		-webkit-border-radius: 34px
		border-radius: 34px
		color: #fff
		z-index: 5
		+tr(.5s)
		+placeholder
			color: $n
		&:focus
			width: 240px
			+r(800)
				width: 190px
	.btn-search
		position: absolute
		top: 25px
		right: 15px
		+s(lupa)
		background-color: $n
		z-index: 10
		&:hover
			+ input
				width: 200px

.radio
	display: inline-block
	color: #8a93a3
	position: relative
	cursor: pointer
	margin-right: 40px
	&:last-child
		margin-right: 10px
	input
		position: absolute
		left: 0
		top: 4px
		opacity: 0
		z-index: 2
		&:checked
			+ span
				&:before
					background-color: $blue
				&:after
					content: ""
					position: absolute
					left: -33px
					top: 8px
					z-index: 12
					+s(check)
	span
		position: relative
		display: inline-block
		padding-left: 0px
		margin: 0 0 5px 40px
		font-size: 16px
		color: $black
		+osl
		&:before
			content: ""
			position: absolute
			left: -40px
			top: 0
			background-color: #dfe5e7
			width: 24px
			height: 24px
			z-index: 10


.pager
	margin: 120px 0 90px
	+osl
	+clr
.pages__top
	position: relative
	+clr
.page
	display: inline-block
	font-size: 0
	+vcenter
	+clr
	li
		float: left
		margin-right: -1px
		line-height: 45px
		height: 45px
		border: 1px solid #d8dde2
		-webkit-border-radius: 5px
		border-radius: 5px
	a
		font-size: 18px
		color: $black
		display: block
		padding: 0 15px
		+tr(.3s)
		&:hover,
		&.is_active
			color: $purpur
.page__prev,
.page__next
	display: inline-block
	vertical-align: top
	width: 160px
	line-height: 45px
	height: 45px
	padding: 1px 20px 0
	border: 1px solid #d8dde2
	-webkit-border-radius: 5px
	border-radius: 5px
	+box
	&:hover
		-webkit-box-shadow: 0 0 7px rgba(0, 0, 0, .5) inset
		box-shadow: 0 0 10px rgba(0, 0, 0, .5) inset
	span
		float: right
		color: $black
		font-size: 18px
		+osl
	i
		float: left
		margin-top: 10px
.page__prev
	float: left
.page__next
	float: right
.pager__input
	margin: 25px auto 0
	text-align: center
	vertical-align: top
	&_wrap
		display: inline-block
		line-height: 45px
		height: 45px
		padding: 0 20px
		border: 1px solid #d8dde2
		-webkit-border-radius: 5px
		border-radius: 5px
		+box
		i
			float: right
			margin-top: 10px
.page__number
	background-color: $n
	font-size: 16px
	+placeholder
		font-size: 14px
		color: $black
		+osl
.categories
	width: 100%
	margin: 90px 0
	+clr

.category
	width: 25%
	float: left
	padding-right: 15px
	+box
	+r(900)
		width: 50%
	+r(480)
		width: 100%
	a
		&:hover
			color: $blue
		&.is_active
			color: $blue